

























import uniInput from '../../components/uni-input.vue';
import countDown from '../../components/countDown/countDown.vue';
import Validator from '../../utils/validator.js';
import strategys from '../../utils/strategy.js';
export default {
	components: {
		uniInput,
		countDown
	},
	computed: {
		_disabled() {
			if (this.phone && this.password && this.smscode) {
				return false
			}
			return true;
		}
	},
	data() {
		return {
			disabled: false,
			type: 1,
			phone: '',
			password: '',
			smscode: ''
		}
	},
	onShow() {
		console.log(this.disabled)
	},
	methods: {
		getCode() {
			var errorMsg = this.validateMobileFunc();
			if (errorMsg) {
				this.Util.Toast.toast(errorMsg);
				return
			}
			console.log(this.disabled)
			if(!this.disabled){
				this.disabled = true;
				this.API.sendMsg({
					phone: this.phone,
					type: 2
				}).then(res => {
					// 短信发送成功才能开始倒计时
					this.$refs.countDown.countDown();
					this.Util.Toast.success("验证码已发送");
					setTimeout(()=>{ this.disabled = false;  }, 6 * 1000)
				}).catch((res)=>{
					this.disabled = false;
				});
			}							
		},
		save() {
			var errorMsg = this.validateFunc();
			if (errorMsg) {
				this.Util.Toast.toast(errorMsg);
				return
			}
			var params = {
				type: this.type,
				phone: this.phone,
				password: this.password,
				smscode: this.smscode
			}
			this.API.forgetPas(params).then(res => {
				this.Util.Toast.success("保存成功");
				setTimeout(() => {
					uni.navigateBack();
				}, 2000)
			})

		},
		validateMobileFunc() {
			var validator = new Validator(strategys); // 创建一个Validator对象
			/* 添加一些效验规则 */
			validator.addRules(this.phone, [{
					'strategy': 'mobile',
					'errorMsg': '请输入正确的手机号'
				}],
				function() {
					console.log('验证通过');
				}
			);
			var errorMsg = validator.start(); // 执行验证函数，并返回效验结果
			return errorMsg;
		},
		validateFunc() {
			var validator = new Validator(strategys); // 创建一个Validator对象
			validator.addRules(this.phone, [{
					'strategy': 'mobile',
					'errorMsg': '请输入正确的手机号'
				}],
				function() {
					console.log('验证通过');
				}
			);
			validator.addRules(this.smscode, [{
					'strategy': 'minLength:4',
					'errorMsg': '请输入短信验证码'
				}],
				function() {
					console.log('验证通过');
				}
			);
			validator.addRules(this.password, [{
					'strategy': 'minLength:5',
					'errorMsg': '密码长度不能少于5个字符'
				}],
				function() {
					console.log('验证通过');
				}
			);

			var errorMsg = validator.start(); // 执行验证函数，并返回效验结果
			return errorMsg;
		}
	}
}
